.ReactCollapse--collapse {
  max-width: auto;
  border: 1px solid gray;
  border-radius: 5px;
  background-color: white;
  margin-top: 1%;
  margin-left: 1%;
  margin-right: 1%;
  margin-bottom: 1%;
  cursor: pointer; }

.ReactCollapse--collapse:hover {
  border-color: cyan; }

.logmetadata {
  padding: 1%; }

@media (min-width: 768px) {
  .logdatetime {
    float: left;
    cursor: pointer; }
  .logtitle {
    cursor: pointer; }
  .logtags {
    float: right;
    cursor: pointer; }
  .clearboth {
    clear: both; } }



